import React, { useState, useEffect } from "react";
import axios from "axios";
import { useNavigate, useParams } from "react-router-dom";
import style from "../Style/AddPhotoStyle.css";
import toast from "react-hot-toast";

const UpdateCard = () => {
  const id = useParams().id;
  const userId = localStorage.getItem("userId");
  const navigate = useNavigate();
  const [imageTitle, setImageTitle] = useState("");
  const [imageDescription, setImageDescription] = useState("");
  const [imageLink, setImageLink] = useState("");

  // Authentication
  const loginNavigate = () => {
    if (!localStorage.getItem("userId")) {
      navigate("/login");
    }
  };

  // Get Blogs
  const getBlogDetails = async () => {
    try {
      const { data } = await axios.get(`/api/v1/blog/get-blog/${id}`);
      if (data?.success) {
        setImageTitle(data?.blog.title);
        setImageDescription(data?.blog.description);
        setImageLink(data?.blog.image);
      }
    } catch (error) {
      // console.log(error);
    }
  };

  // Form Submit
  const handleSubmit = async (e) => {
    // Authentication
    if (!imageTitle) {
      toast.error("Enter title");
      return;
    }

    if (!imageDescription) {
      toast.error("Enter Description");
      return;
    }

    if (!imageLink) {
      toast.error("Enter image link");
      return;
    }
    e.preventDefault();
    try {
      const { data } = await axios.put(`/api/v1/blog/update-blog/${id}`, {
        title: imageTitle,
        description: imageDescription,
        image: imageLink,
        user: userId,
      });
      if (data?.success) {
        navigate("/my-post");
        toast.success("Post Updated");
      }
    } catch (error) {
      // console.log(error);
      toast.error("Error while updating post");
    }
  };

  useEffect(() => {
    getBlogDetails();
  }, [id]);
  useEffect(() => {
    loginNavigate();
  }, []);
  return (
    <div className="mainContainer">
      <div className="container">
        <h1>Update post</h1>
        <form>
          <input
            type="text"
            id="imageTitle"
            placeholder="Image Title"
            value={imageTitle}
            onChange={(e) => setImageTitle(e.target.value)}
            required
          />
          <textarea
            id="imageDescription"
            placeholder="Image Description"
            value={imageDescription}
            onChange={(e) => setImageDescription(e.target.value)}
            required
          />
          <textarea
            id="imageLink"
            placeholder="Image Link"
            value={imageLink}
            onChange={(e) => setImageLink(e.target.value)}
            required
          />
          <button onClick={handleSubmit}>Add Post</button>
        </form>
      </div>
    </div>
  );
};

export default UpdateCard;
