import React, { useState } from "react";
import style from "../Style/CardStyle.css";
import editingPhoto from "../photo/editing.png";
import trashPhoto from "../photo/trash.png";
import { useNavigate } from "react-router-dom";
import axios from "axios";

const Card = ({
  isUser,
  imageUrl,
  title,
  description,
  author,
  // likeCount,
  id,
}) => {
  // States
  // const [likes, setLikes] = useState(likeCount);
  const navigate = useNavigate();

  const handleDelete = async () => {
    try {
      const { data } = await axios.delete(`api/v1/blog/delete-blog/${id}`);
      if (data?.success) {
        alert("Blog is deleted");
        window.location.reload();
      }
    } catch (error) {
      console.log(error);
    }
  };

  const handleEdit = () => {
    navigate(`/update-card/${id}`);
  };

  // const handleLike = () => {
  //   setLikes(likes + 1);
  // };

  return (
    <div className="card">
      {isUser && (
        <div className="myPhoto">
          <img onClick={handleEdit} src={editingPhoto} alt="" />
          <img onClick={handleDelete} src={trashPhoto} alt="" />
        </div>
      )}
      <img src={imageUrl} alt={title} className="card-image" />
      <div className="card-details">
        <div className="card-author">{author}</div>
        <h3 className="card-title">{title}</h3>
        <p className="card-description">{description}</p>
        {/* <div className="card-buttons">
          {true && (
            <button className="like" onClick={handleLike}>
              Like {likes}
            </button>
          )}
          {true && <button className="dislike"> like {likes}</button>}
          <button>Comments</button>
        </div> */}
      </div>
    </div>
  );
};

export default Card;
