import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import style from "../Style/NavbarStyle.css";
import toast from "react-hot-toast";

const Navbar = () => {
  const navigate = useNavigate();
  const [navbarLink, setNavbarLink] = useState("navbar-links");
  const myUserId = localStorage.getItem("userId");
  const toggleHandle = () => {
    if (navbarLink == "navbar-links") {
      setNavbarLink("navbar-links active");
    } else {
      setNavbarLink("navbar-links");
    }
  };
  const handleClick = () => {
    try {
      localStorage.clear();
      navigate("/login");
      toast.success("Logout");
    } catch (error) {}
  };

  return (
    <>
      {myUserId && (
        <nav className="navbar">
          <div className="brand-title">Insta</div>
          <a href="#" className="toggle-button" onClick={toggleHandle}>
            <span className="bar" />
            <span className="bar" />
            <span className="bar" />
          </a>
          <div className={navbarLink}>
            <ul>
              <li>
                <Link to="/">All Post</Link>
              </li>
              <li>
                <Link to="/my-post">My Post</Link>
              </li>
              <li>
                <Link to="/create-post" className="flex items-center">
                  Create Post
                </Link>
              </li>
              <li className="buttonLogout" onClick={handleClick}>
                Logout
              </li>
            </ul>
          </div>
        </nav>
      )}
    </>
  );
};

export default Navbar;
